<template>
  <div class="container">
    <div class="card">
      <div class="card-action">
        <h4 class="center orange-text text-darken-3">Aslanyan`s Chat v1.3</h4>
        <hr>
        <div class="card-content" ref="content">
          <span></span>
          <ul class="messages">
            <li v-for="chat of chats"
                :class="name == chat.name ? 'current delete' : 'another' " @click.self="deleteData">

              <input type="hidden" name="hidden_name" :value="name" v-if="name == chat.name">
              <span class="name red-text text-darken-2">{{ name == chat.name ? 'You' :  chat.name}}</span>
              <p class="message aqua-text" @load="change">{{chat.text}}</p>
              <span class="time cyan-text lighten-4">{{chat.createdAt.time}}</span>
              <span class="date cyan-text lighten-4">{{chat.createdAt.date}}</span>
         
            </li>
          </ul>
        </div>
        <new-message :name="name" :content="this.$refs['content']" />
      </div>
    </div>
  </div>
</template>

<script>
import NewMessage from '@/components/NewMessage';
import db from '../firebase/init.js';
import util from '../utils/util';


export default {
    components:{
      NewMessage,
    },
    data(){
      return{
        name:document.cookie.split('=')[1]
      }
    },
    firestore() {
      return {
        chats: db.collection('chats').orderBy('createdAt')
                
      }
    },
    methods:{
      /**
       * Delete Current elemnt data
       * @augments Event
       */
      deleteData(e){
          let currentEl = e.target; // Element in whcih was clicked
          if(currentEl.classList[0] == 'current'){ 
              let name = currentEl.firstChild.value; // Get name from hidden input 
              let text = currentEl.childNodes[2].textContent; // Get Text 

              /** Find in db by name then compare text and if equal , delete it */
              db.collection('chats').where('name' , '==' , name).get().then(prom => {
                  prom.forEach(el => {
                      if(el.data().text == text){
                         util.Handler('Your Message was deleted!' , false , 2000);
                         el.ref.delete();
                      }
                  });
              })
          }
      },
      change(e){
        console.log(e);
        
      }
    },
    updated(){

       let messages  = document.querySelectorAll('.message');
       let regExp = /^https?:(\/\/)(\w+)\.(\w{2,10})(\.\w{2,10})?\//

       // Link text , if the text match to regExp
       messages.forEach(el => {

          let text = el.textContent; 

          if(text.match(regExp)){
        
            
            el.textContent = '';
            let a = document.createElement('a');
            a.setAttribute('href' , text);
            a.setAttribute('target' , '_blank');
            a.textContent = text;

           return  el.appendChild(a);

          }
         
       })
       
    }


}
</script>

<style lang="scss" scoped>
.card-content{
  max-height: 60vh;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  z-index: 1;
}
.name{
    text-transform: capitalize;
    margin-right: 0.5rem;
  }
.message{
  word-break: break-all;
  font-weight: 600;
  padding: .5rem;
}
.time , .date{display: block; text-align: right;}
.date{ font-size: .75rem;}
.messages{ 
  margin-top: .5rem;
  li{
    margin-top: 1rem;
    padding: 0.5rem;
    border-radius: 10px;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: calc(50% - 8px);
      transform: rotate(45deg);
      z-index: -1;
      height: 16px;
      width: 16px;
      background: inherit;
    }
  }
  .current , .another{
    clear:both;
  }
  .current{
    background: #ede7f6;
    float: right;
    &:after{ right: -8px; }
  }
  .another{
    background:#bbdefb;
    float: left;
    &:after{left:-8px;}

  }
}
.delete{
  position: relative;
  cursor: pointer;
  overflow: hidden;
  &:hover  {
      transform: scale(1.1);
      &:before{
      font-family: 'Material Icons';
      content: 'delete';
      font-size: 2rem;
      color:#fff;
      text-align: center;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 50%;
      background:rgba(#333 , .65);
    }
  }
}
</style>




